import { useAuth0 } from "@auth0/auth0-react";
import React, { useState } from "react";
import { Button, Offcanvas } from "react-bootstrap";
import { postData } from "../apiCalls/postData";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";

const Date = ({ eventObj, dateObj }) => {
  const [show, setShow] = useState(false);
  const handleShow = () => setShow(true);
  const handleClose = () => setShow(false);

  const [response, setResponse] = useState("");
  const [redirect, setRedirect] = useState(null);

  const { user } = useAuth0();
  const { date, time, price, capacity, occupied } = dateObj;
  const { name, location } = eventObj;
  const { getAccessTokenSilently } = useAuth0();
  const dateID = dateObj._id;

  const createTicket = async () => {
    console.log(`la id es ${dateID}`);
    const token = await getAccessTokenSilently();
    const reqBody = {
      dateID,
      ownerName: user.name,
      ownerEmail: user.email,
    };
    postData("events/ticket/create-ticket", reqBody, token)
      .then((res) => res.json())
      .then((res) => {
        setResponse(res.msg + "");
        setRedirect(res.id);
      });
  };

  return (
    <>
      <div className="card">
        <div className="card-body">
          <div className="row">
            <div className="col-md">
              <h5 className="card-title">{`${date} at ${time}`}</h5>
              <small className="text-muted">{`Capacity: ${
                capacity - occupied
              } of ${capacity} (${100 / (capacity / occupied)}%) `}</small>{" "}
            </div>
            <div className="col-sm">
              <h5>{`$${price}`}</h5>
            </div>
            <div className="col-sm">
              <Button onClick={handleShow}>Buy Ticket</Button>
            </div>
          </div>
          <div className="row">
            <div className="progress" style={{ height: "2px" }}>
              <div
                className="progress-bar"
                role="progressbar"
                style={{ width: 100 / (capacity / occupied) + "%" }}
              ></div>
            </div>
          </div>
        </div>
      </div>

      {redirect ? <Redirect to={`/ticket/${redirect}`} /> : <> </>}

      <Offcanvas show={show} onHide={handleClose} placement="end">
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>Buy Ticket</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <h4>Event Details</h4>
          <table className="table">
            <tbody>
              <tr>
                <td>Event</td>
                <td>{name}</td>
              </tr>
              <tr>
                <td>Location</td>
                <td>{location}</td>
              </tr>
              <tr>
                <td>Date</td>
                <td>{date}</td>
              </tr>
              <tr>
                <td>Time</td>
                <td>{time}</td>
              </tr>
            </tbody>
          </table>

          <hr />

          <h4>Your Details</h4>
          <table className="table">
            <tbody>
              <tr>
                <td>Name</td>
                <td>{user.name}</td>
              </tr>
              <tr>
                <td>Email</td>
                <td>{user.email}</td>
              </tr>
            </tbody>
          </table>

          <hr />

          <h4>Checkout </h4>

          <table className="table">
            <tbody>
              <tr>
                <td>Ticket Price</td>
                <td>{`$ ${price}`}</td>
              </tr>
              <tr>
                <td>Discount (-100%)</td>
                <td>{`$-${price}  `}</td>
              </tr>
              <tr>
                <td>Tax (21%)</td>
                <td>{`$ 0`}</td>
              </tr>
            </tbody>
          </table>
          <hr />
          <h5>{`Total:  $${price - price}`}</h5>
          <div className="col m-1">
            <Button className="green" onClick={() => createTicket(dateID)}>
              Pay Total
            </Button>
            {response}
          </div>
        </Offcanvas.Body>
      </Offcanvas>
    </>
  );
};

export default Date;
