const ScannedTicketInfo = ({
  ownerName,
  date,
  time,
  name,
  location,
  ownerEmail,
}) => (
  <>
    <h3>{ownerName}</h3>
    <h5>{`${date} @ ${time}`}</h5>
    <p>{`${name} in ${location}`}</p>
    <p>{`Email: ${ownerEmail}`}</p>
  </>
);

export default ScannedTicketInfo;
