const TicketPreview = ({
  name,
  location,
  date,
  time,
}) => (
  <>
    <div className="card-body">
      <div className="row pb-3">
        <h2>{name}</h2>
      </div>
      <div className="row">
        <div className="col-sm">
          <h3>{location}</h3>
          <hr />
          <h4>{date}</h4>
          <h5>{`at ${time}`}</h5>
        </div>

      </div>
    </div>
  </>
);

export default TicketPreview;
