const serverUrl = process.env.REACT_APP_SERVER_URL;

export function getData(route){
  const url = serverUrl + "/api/" + route;
  return fetch(url)
  .then(res => res.json())
}

 export function getDataProtected (route, token){
  const url = serverUrl + "/api/" + route;
  return fetch(url, {
    method: "POST",
    headers: {
      Authorization: `Bearer ${token}`,
    },
  })
    .then(res => res.json())
    .catch((e) => console.error(e));
}



