import React from "react";
import MyForm from "../components/form";
import FormElement from "../components/form-element";
import FormRow from "../components/form-row";

const EventOwner = ({ id }) => {
  return (
      <MyForm
        initialValues={{
          date: "",
          time: "",
          price: 0,
          capacity: 0,
        }}
        formTitle="New Date"
        url={`events/${id}/new-date`}
      >
        <FormRow>
          <FormElement type="date" name="date" title="Date" />
          <FormElement type="time" name="time" title="Time" />
        </FormRow>
        <FormRow>
          <FormElement type="number" name="price" title="Price" />
          <FormElement type="number" name="capacity" title="Capacity" />
        </FormRow>
      </MyForm>
  );
};

export default EventOwner;
