import React from "react";
import { NavLink } from "react-router-dom";

const Event = (props) => {
  const { _id, name, description, location, imgURL } = props.event;

  return (
    <>
      <NavLink
        to={`/event/${_id}`}
        exact
        className="floating-card card col-sm-7"
      >
        <img className="card-img-top" src={imgURL} alt="Event IMG" />
        <div className="card-body">
          <h4 className="card-title">{name}</h4>
          <div>
            <p className="card-text">{description}</p>

            <p className="card-text">{location}</p>
          </div>{" "}
        </div>
      </NavLink>
    </>
  );
};


export default Event;
