import React from "react";
import MyForm from "./form";
import FormElement from "./form-element";

const CreateEvent = () => {
  return (
    <MyForm
      initialValues={{
        eventName: "",
        description: "",
        location: "",
        imgURL: "",
      }}
      formTitle="Create a new event"
      url="events/create-new"
      redirectURL = {eventURL => `/event/${eventURL}`}
    >
      <FormElement name="eventName" type="text" title="Name" />
      <FormElement name="description" type="text" title="Description" textarea />

      <FormElement name="location" type="text" title="Location" optional />
      <FormElement name="imgURL" type="text" title="URL of Image" />
    </MyForm>
  );
};

export default CreateEvent;
