import React, { useEffect, useState } from "react";
import { getDataProtected } from "../apiCalls/getData";
import { useAuth0 } from "@auth0/auth0-react";
import { Loading } from "../components";
import { Alert } from "react-bootstrap";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import TicketPreview from "../components/ticketPreview";

const MyTicketsPage = () => {
  const { getAccessTokenSilently } = useAuth0();
  const [tickets, setTickets] = useState([]);
  const [loading, setLoading] = useState(true);
  const [redirect, setRedirect] = useState(null);

  useEffect(() => {
    async function getTickets() {
      const token = await getAccessTokenSilently();
      getDataProtected("events/ticket/user", token).then((res) => {
        setTickets(res);
        setLoading(false);
      });
    }
    getTickets()
     // eslint-disable-next-line
  }, []);

  return (
    <>
      {redirect ? <Redirect to={redirect} /> : null}
      <div className="container mb-5">
        <h1 className="mb-4">My Tickets</h1>
      </div>
      <hr />

      {loading ? (
        <Loading />
      ) : tickets ? (
        tickets.map((ticket) => (
          <div
            className="m-3 floating-card card"
            onClick={() => setRedirect(`/ticket/${ticket.ticketID}`)}
          >
            <TicketPreview {...ticket} />
          </div>
        ))
      )  :(
        <Alert>You don't have any tickets</Alert>
      ) }
    </>
  );
};

export default MyTicketsPage;
