import { useEffect, useState } from "react";
import { Loading } from ".";

const Ticket = ({
  name,
  location,
  date,
  time,
  price,
  ownerEmail,
  ownerName,
  ticketID,
}) => {


  useEffect(() => {
    const tID = ticketID

    fetch(
      `https://api.qrserver.com/v1/create-qr-code/?size=150x150&data=${tID}`
    )
      .then((response) => response.blob())
      .then((imageBlob) => {
        setImageObjectURL(URL.createObjectURL(imageBlob));
      });
       // eslint-disable-next-line
  }, []);

  const [imageObjectURL, setImageObjectURL] = useState(null);



  return (
    <>
      <div className="p-3 card m-2">
        <div className="row pb-3">
          <h2>{name}</h2>
        </div>
        <div className="row">
          <div className="col-sm">
            <h3>{location}</h3>
            <hr />
            <h4>{date}</h4>
            <h5>{`at ${time}`}</h5>
          </div>

          <div className="col-md">
            <h4>{`$${price}`}</h4>
            <hr />
            <h5>{ownerEmail}</h5>
            <h5>{ownerName}</h5>
          </div>
          <div className="col-sm p-3">
            {imageObjectURL ? (
              <a href={imageObjectURL} className="card-floating">
                <img
                  src={imageObjectURL}
                  alt={ticketID}
                  className="border p-3"
                  style={{ width: "100%" }}
                />
              </a>
            ) : (
              <div className="border p-3">
                <h5>Loading QR...</h5>
                <Loading />{" "}
              </div>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default Ticket;
