// src/views/external-api.js
import React, { useEffect, useState } from "react";
import { getDataProtected } from "../apiCalls/getData";
import Event from "../components/event";
import CreateEvent from "../components/create-event";
import Loading from "../components/loading";
import { useAuth0 } from "@auth0/auth0-react";

const Alert = ({ msg }) => (
  <div
    classNameName="alert alert-warning alert-dismissible fade show"
    role="alert"
  >
    <strong>{JSON.stringify(msg)}</strong>
  </div>
);

const MyEvents = () => {
  const [events, setEvents] = useState([]);
  const [error, setError] = useState("");
  const [loadingEvents, setLoadingEvents] = useState(true);
  const { getAccessTokenSilently } = useAuth0();

  useEffect(() => {
    async function getEvents() {
      const token = await getAccessTokenSilently();
      getDataProtected("events/user-events", token)
        .then((res) => {
          setEvents(
            res.map((event) => <Event event={event} key={event._id} />)
          );
          setLoadingEvents(false);
        })
        .catch((err) => setError(" " + err));
    }

    getEvents();
     // eslint-disable-next-line
  }, []);

  return (
    <>
      <div className="container mb-5">
        <h1 className="mb-4">My Events</h1>

        <CreateEvent />
      </div>
      <hr />

      <div className="container">{error ? <Alert msg={error} /> : events}</div>
      {loadingEvents ? <Loading /> : events}
    </>
  );
};

export default MyEvents;
