import React from "react";
import { Route, Switch } from "react-router-dom";
import { useAuth0 } from "@auth0/auth0-react";

import { NavBar, Loading, Footer } from "./components";
import { Home, Profile, Test, Events, EventPage } from "./views";
import ProtectedRoute from "./auth/protected-route";

import "./app.css";
import TicketPage from "./views/ticketPage";
import MyTicketsPage from "./views/my-tickets";
import MyEvents from "./views/my-events";
import TicketScanner from "./views/qrReader";

const App = () => {
  const { isLoading } = useAuth0();

  if (isLoading) {
    return <Loading />;
  }

  return (
    <div id="app" className="d-flex flex-column h-100">
      <NavBar />
      <div className="container flex-grow-1">
        <Switch>
          <Route path="/" exact component={Home} />
          <ProtectedRoute path="/profile" component={Profile} />
          <ProtectedRoute path="/events" component={Events} />
          <ProtectedRoute path="/test" component={Test} />
          <Route path="/event/:id" component={EventPage} />
          <Route path="/ticket/:id" component={TicketPage} />
          <Route path="/scan" component={TicketScanner} />

          <ProtectedRoute path="/my-tickets" component={MyTicketsPage} />
          <ProtectedRoute path="/my-events" component={MyEvents} />


          <ProtectedRoute path="/test" component={Test} />


        </Switch>
      </div>
      <Footer />
    </div>
  );
};

export default App;
