import React, { useState } from "react";
import { Formik, Form } from "formik";
import { postData } from "../apiCalls/postData";
import { useAuth0 } from "@auth0/auth0-react";
import { Offcanvas, Button, Alert } from "react-bootstrap";
import Loading from "./loading";
import { Redirect } from "react-router-dom";

const SubmitButton = () => <Button type="submit">Submit</Button>;

const ResComponent = ({ status, msg }) => (
  <Alert variant={status ? "success" : "danger"} className="m-3">
    {status ? `Success: ${msg}` : `Error: ${msg}`}{" "}
  </Alert>
);

const MyForm = ({ formTitle, initialValues, url, children, redirectURL }) => {
  const [msg, setMsg] = useState(null);
  const [status, setStatus] = useState(0);
  const [submitted, setSubmitted] = useState(false);
  const [resID, setResID] = useState(null);

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const { getAccessTokenSilently } = useAuth0();

  return (
    <>
      <Button className="gray" onClick={handleShow}>
        {formTitle}
      </Button>

      <Offcanvas show={show} onHide={handleClose}>
        <Offcanvas.Header closeButton>
          <Offcanvas.Title> {formTitle}</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <Formik
            initialValues={initialValues}
            onSubmit={async (values, { setSubmitting }) => {
              setSubmitting(true);
              const token = await getAccessTokenSilently();
              postData(url, values, token)
                .then((res) => {
                  setStatus(res.ok);
                  return res.json();
                })
                .then((res) => {
                  setMsg(res.msg);
                  setResID(res.id);
                })
                .then(() => {
                  setSubmitting(false);
                  setSubmitted(true);
                });
            }}
          >
            {({ isSubmitting }) => (
              <Form className="container">
                {children}
                {isSubmitting ? <Loading /> : <SubmitButton />}
              </Form>
            )}
          </Formik>

          {submitted ? (
            <>
              <ResComponent status={status} msg={msg} />
              {redirectURL ? <Redirect to={redirectURL(resID)} /> : null}
            </>
          ) : null}
        </Offcanvas.Body>
      </Offcanvas>
    </>
  );
};

export default MyForm;
