// src/views/external-api.js

import React, { useState } from "react";
import { useAuth0 } from "@auth0/auth0-react";
import { postData } from "../apiCalls/postData";
import { getData } from "../apiCalls/getData";

const CreateEvent = () => {
  const [responseMessage, setResponseMessage] = useState("aa");
  const [message, setMessage] = useState("");
  const [messages, setMessages] = useState([]);

  const { getAccessTokenSilently } = useAuth0();

  const getMessages = () => {
    getData("messages/get-messages").then((res) => setMessages(res));
  };

  const send = async () => {
    const token = await getAccessTokenSilently();
    postData("messages/create-message", { message: message }, token).then(
      (res) => setResponseMessage(res)
    );
  };

  return (
    <div className="container">
      <input
        type="text"
        className="input"
        value={message}
        onChange={(e) => setMessage(e.target.value)}
      />

      <button type="button" className="btn btn-primary" onClick={send}>
        Send
      </button>

      <button type="button" className="btn btn-primary" onClick={getMessages}>
        Get Messages
      </button>

      {responseMessage && (
        <div className="mt-5">
          <h6 className="muted">Result</h6>
          <div className="container-fluid">
            <div className="row">
              <code className="col-12 text-light bg-dark p-4">
                {JSON.stringify(responseMessage)}
              </code>
            </div>
          </div>
          <h6 className="muted">Mensajes</h6>
          <div className="container-fluid">
            <div className="row">
              <code className="col-12 text-light bg-dark p-4">
                {messages.map((msg) => (
                  <h4>{msg.msg}</h4>
                ))}
              </code>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default CreateEvent;
