import React, { useState, useEffect } from "react";
import { getData } from "../apiCalls/getData";
import Date from "./date";
import Loading from "./loading";

const EventBuyer = ({ id, description, location, imgURL }) => {
  const [dates, setDates] = useState([]);
  const [error, setError] = useState(null);
  const [loadingDates, setLoadingDates] = useState(true);

  useEffect(() => {
    getData(`events/${id}/dates`)
      .then((res) => {
        setDates(res);
        setLoadingDates(false);
      })
      .catch((err) => setError(" " + err));
       // eslint-disable-next-line
  }, []);

  const datesComponents = dates.map((dateObj) => {
    return <Date dateObj={dateObj} eventObj={{ id, description, location, imgURL }} key={dateObj._id} />;
  });

  return (
    <>
      <div>
        {error}
        <div className="card m-3 shadow">
          <div className="card-body">
            <img src={imgURL} className="img-fluid mb-4" alt="" />
            <h5>{`Location: ${location}`}</h5>
            <p>{` ${description}`}</p>
          </div>
        </div>
        <div className="card m-3 shadow">
          <div className="card-body">
            <h3 className="m-3"> Dates </h3>
            {loadingDates ? (
              <Loading />
            ) : (
              <>
                {datesComponents}
              </>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default EventBuyer;
