import React, { useState, useEffect } from "react";
import { getData } from "../apiCalls/getData";
import { useAuth0 } from "@auth0/auth0-react";
import EventBuyer from "../components/event-buyer";
import EventOwner from "../components/event-owner";
import Loading from "../components/loading";

const EventPage = (props) => {

  const id = props.match.params.id;
  const [eventData, setEventData] = useState({});
  const [loading, setLooading] = useState(true)
  const [error, setError] = useState("");
  const { name, description, location, imgURL, creator } = eventData;
  const { user } = useAuth0();

  const isOwner = eventData.creator === user.sub;

  useEffect(() => {

      getData(`events/id/${id}`)
      .then((res) => {
        setEventData(res);
        setLooading(false)
      })
      .catch((err) => setError(" " + err));


  });

  if (error) return error;

  if(loading) return <Loading />

  return (
    <>
      <div className="container mb-5">
        <h1 className="mb-4 placeholder-glow" >{name}</h1>
        <h4 className="placeholder-glow">{location} </h4>
      </div>

      <hr />


      <EventBuyer
        imgURL={imgURL}
        description={description}
        creator={creator}
        id={id}
        location={location}
        name={name}
      />

      {isOwner ? <EventOwner id={id} /> : <></>}
    </>
  );
};

export default EventPage;
